/**
 * AMD module to trigger an event when selecting a user from the list
 *
 * @module     local_cltools/user_selector
 * @copyright 2021 - CALL Learning - Laurent David <laurent@call-learning.fr>
 * @license   http://www.gnu.org/copyleft/gpl.html GNU GPL v3 or later
 */
import jQuery from 'jquery';

/**
 * Initialise field handler.
 *
 * @param {Number} uniqueid
 */
export const init = ({uniqueid}) => {
    jQuery('.user-select[data-user-id]').each(
        (index, el) => {
            const element = jQuery(el);
            element.click(() => {
                const userid = element.data('user-id');
                const fullname = element.data('user-fullname');
                jQuery('body').trigger(
                    'user-selected-event',
                    [uniqueid, userid, fullname]
                    );
            });
        }
    );
};
